@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

body{background-color:#fff;font-family:'Roboto Condensed', sans-serif;text-align:center;font-size:1.2em}.NavBar{background-color:#373F47}.NavBar ul{list-style-type:none;font-weight:bold;margin:0}.NavBar ul li{display:inline-block;padding:20px}.NavBar ul li a{text-decoration:none;color:#fff}.NavBar ul li a:hover{color:grey}.NavBar ul li a:active{color:grey}.episode-card{color:#fff;margin:50px;padding:20px;max-width:20%;display:inline-block;min-height:200px;min-width:200px;text-align:center;background-color:#373F47}.episode-card div{padding:5px 0}.episode-card button{padding:10px;font-weight:bold;margin:10px;border:none;background-color:#fff}.character-card{color:#fff;margin:50px;padding:20px;max-width:20%;display:inline-block;min-height:200px;width:400px;text-align:center;background-color:#373F47}.character-card img{max-width:200px}.character-card div{padding:5px 0}.character-card button{padding:10px;font-weight:bold;margin:10px;border:none;background-color:#fff}

